import { useEffect, useState } from 'react';

// MUI
import {
    Button,
    Card,
    CardContent,
    CardHeader,
    Divider,
    ListItem,
    Typography
} from '@mui/material';

// TYPES
import { ApiNew_ } from './DataTypes';
import { apiDate } from '../../Commons/Help';

const def_news: Array<ApiNew_> = [
    "......... ......... ........ .... .",
    "...... ... ...... .... .... ..... .",
    ".............. ....... ... .... . ."
]

type NewsProps = {
    date: Date
}

export default function News({ date }: NewsProps) {
    const [open, setOpen] = useState<boolean>(false);
    const [news, setNews] = useState<Array<ApiNew_>>(def_news);

    const openSwitch = () => {
        setOpen(!open);
    };

    useEffect(() => {
        fetch('sculis.uceumice.com/v1/api/s/test/news/' + apiDate(date)).then(
            (response) => response.json()
        ).then(
            (data) => setNews(data)
        );
    }, [news]);

    return (
        <ListItem disableGutters disablePadding>
            <Card
                sx={{
                    borderRadius: '0',
                    marginTop: "0",
                    marginBottom: "20px",
                    border: "none",
                    width: "100%"
                }}
                variant="outlined"
            >
                <CardHeader
                    disableTypography
                    title={
                        <div
                            style={{ display: "flex", justifyContent: "center" }}
                        >
                            <Button
                                sx={{
                                    // GRADIENT COLOR
                                    background: (theme) => theme.custom.gradients.button,
                                    WebkitBackgroundClip: "text",
                                    WebkitTextFillColor: "transparent",

                                    fontWeight: "bold"
                                }}
                                onClick={openSwitch}
                                disabled={news.length <= 0}
                            >
                                {news.length > 0 ? "Nachrichten" : "Keine Nachrichten"}
                            </Button>
                        </div>
                    }
                />
                {
                    open && news.length > 0 ? (
                        < CardContent>
                            {
                                news.map(
                                    function (value, index, arr) {
                                        return (
                                            <div >
                                                <div
                                                    style={{
                                                        margin: "10px 0px",
                                                        display: "flex"
                                                    }}
                                                >
                                                    <Typography
                                                        sx={{
                                                            marginRight: "10px"
                                                        }}
                                                        fontWeight="bold"
                                                    >
                                                        {"  "}
                                                    </Typography>
                                                    <Typography
                                                        variant="body2"
                                                    >
                                                        {value}
                                                    </Typography>
                                                </div>
                                                <Divider />
                                            </div>

                                        )
                                    }
                                )
                            }
                        </CardContent>
                    ) : (
                        <Divider />
                    )
                }
            </Card >
        </ListItem>
    )
};