import React from "react";

// MUI
import {
    Container,
    TextField,
    Typography
} from "@mui/material";

// Picker
import {
    LocalizationProvider,
    StaticDatePicker
} from '@mui/lab';

// Date FNS
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import { de } from 'date-fns/esm/locale'

type DatesSubBarProps = {
    date: Date,
    handleDateChange: Function,
}

export default function SBDates(props: DatesSubBarProps) {
    // types
    type DateValueType = Date | null | undefined

    // states
    const [dateValue, setDateValue] = React.useState<DateValueType>(props.date);

    // allowed date-range
    const minDate = new Date(2022, 0, 21)
    const maxDate = new Date()
    maxDate.setDate(maxDate.getDate() + 1)

    return (
        <Container disableGutters>
            <LocalizationProvider dateAdapter={AdapterDateFns} locale={de}>
                <StaticDatePicker value={dateValue}
                    onChange={(newValue: DateValueType) => {
                        setDateValue(newValue);
                        props.handleDateChange(newValue);
                    }}

                    minDate={minDate}
                    // maxDate={maxDate}

                    open={false}
                    openTo="day"

                    
                    // displayStaticWrapperAs="mobile"
                    disableCloseOnSelect={true}

                    onAccept={(newValue: DateValueType) => {
                        setDateValue(newValue);
                        props.handleDateChange(newValue);
                    }}

                    toolbarTitle={
                        <Typography>
                            {"Vertretung fr"}
                        </Typography>
                    }

                    renderInput={(params) => <TextField {...params} />}
                    views={['day']}

                    displayStaticWrapperAs="desktop"
                />
            </LocalizationProvider>
        </Container >
    )
}
