import React from 'react';

// MUI
import {
    Box,
    Button,
    Card,
    IconButton,
    Typography
} from '@mui/material';
import { useTheme } from '@mui/material/styles';

// ICONS
import ExpandMoreRoundedIcon from '@mui/icons-material/ExpandMoreRounded';
import ExpandLessRoundedIcon from '@mui/icons-material/ExpandLessRounded';

// TYPES
import { ApiEntry, ApiStatus } from './DataTypes';

type ColorVariant = {
    dark: string,
    light: string
}

type Colors = {
    raumwechsel: ColorVariant,
    eva: ColorVariant,
    teams: ColorVariant,
    ausfall: ColorVariant,
    divers: ColorVariant,
    vertretung: ColorVariant
}

const statusColors: Colors = {
    raumwechsel: {
        dark: "#40ff7c",
        light: "#4a9e65"
    },
    eva: {
        dark: "#73d1cd",
        light: "#469995"
    },
    teams: {
        dark: "#9f40ff",
        light: "#61009e"
    },
    ausfall: {
        dark: "#db8f8f",
        light: "#a82323"
    },
    divers: {
        dark: "#c4c4c4",
        light: "#636363"
    },
    vertretung: {
        dark: "#fdff91",
        light: "#fff01f"
    }
}

function statusToColor(status: ApiStatus, theme: "light" | "dark"): string {
    return statusColors[status][theme];
}

type EntryProps = {
    entry: ApiEntry
}

export default function Entry({ entry }: EntryProps) {
    const [open, setOpen] = React.useState<boolean>(false);

    const openSwitch = () => {
        setOpen(!open);
    };

    return (
        <Card
            sx={{
                borderRadius: '20px',
                width: "100%"
            }}
            variant="elevation"
            elevation={2}
        >
            <Box
                sx={{
                    display: "flex",
                    justifyContent: "space-between",
                    borderRadius: "999px",
                    width: "100%",
                    height: "max-content"
                }}
            >
                <Button sx={{ paddingX: "5px", color: (theme) => theme.palette.text.primary }}>{entry.subject.id}</Button>
                <Button sx={{ paddingX: "5px", color: (theme) => theme.palette.text.primary }}>{entry.group.id}</Button>
                <Button sx={{ paddingX: "5px", color: (theme) => theme.palette.text.primary, whiteSpace: "nowrap" }}>
                    {
                        entry.room.change == null ? (
                            entry.room._
                        ) : (
                            entry.room._ + "  " + entry.room.change
                        )
                    }
                </Button>
                <IconButton
                    sx={{ paddingX: "5px", color: (theme) => statusToColor(entry.status, theme.palette.mode) }}
                    disabled={entry.info ? false : true}
                    onClick={openSwitch}
                >
                    {
                        open ? (
                            <ExpandLessRoundedIcon />
                        ) : (
                            <ExpandMoreRoundedIcon />
                        )
                    }
                </IconButton>
            </Box>
            {
                open ? (
                    <Card
                        sx={{
                            borderRadius: '25px',
                            marginY: "20px",
                            marginX: "10px",
                        }}

                        variant="elevation"
                        elevation={3}
                    >
                        <div
                            style={{
                                padding: "5px 10px"
                            }}
                        >
                            <Typography
                                variant="body1"
                                sx={{
                                    paddingX: "10px"
                                }}
                                align="justify"
                            >
                                {entry.info}
                            </Typography>
                        </div>
                    </Card>
                ) : (
                    null
                )
            }
        </Card >
    )
};