import { useState, useEffect } from 'react';
import './App.css';

// SUBS
import SculisAppBar from './app/components/AppBar';
import SculisBody from './app/components/AppBody';
import SculisBottomNavigation from './app/components/AppNavi';

// THEMES
import { PinkDark, PinkLight } from './app/themes/PinkDark';
import { ThemeProvider } from '@mui/material/styles';
import { useLocalStorageState, useSessionStorageState, useThemeDetector } from './app/hooks';

// TYPES
export type SculisScreen = "dashboard-screen" | "schedule-screen" | "substitution-screen"
export type SculisTheme = "light" | "dark"


export default function App() {
  const [screen, setScreen] = useSessionStorageState<SculisScreen>("dashboard-screen", "screen")
  const [date, setDate] = useState<Date>(new Date());
  const [theme, setTheme] = useLocalStorageState<SculisTheme>(useThemeDetector() ? 'dark' : 'light', "sculis-theme");

  const changeTheme = () => {
    if (theme === 'light') {
      setTheme('dark');
    } else {
      setTheme('light');
    }
  };

  return (
    <ThemeProvider theme={theme === 'light' ? PinkLight : PinkDark}>
      <div className='fixed
        w-full h-full
        flex flex-col
        select-none'
      >
        <SculisAppBar screen={screen} handleModeChange={() => changeTheme()} mode={theme} sdDate={date} sdDateSetter={(date: Date) => setDate(date)} />
        <SculisBody screen={screen} date={date} />
        <SculisBottomNavigation forScreen={screen}
          // Callbacks (screen change)
          dashboardScreenCallback={() => setScreen("dashboard-screen")}
          scheduleScreenCallback={() => setScreen("schedule-screen")}
          substitutionScreenCallback={() => setScreen("substitution-screen")}
        />
      </div>
    </ThemeProvider>
  );
};
