import React from "react";

// MUI
import {
    Container,
    Box,
    Button,
    FormControl,
    Divider,
    Select,
    MenuItem,
    Fab,
    Typography,
    InputLabel,
    SelectChangeEvent
} from "@mui/material";

import { alpha } from '@mui/material/styles';

// Icons
import AddRoundedIcon from '@mui/icons-material/AddRounded';
import { useLocalStorageState } from "../../../../../hooks";

// TYPES
import { ApiFilter } from "../../../../AppBody/Substitution/DataTypes";

// Types
type Subject = {
    id: string,
    name: string
}
type Group = {
    id: string,
    name: string
}
type Groups = {
    id: string,
    group: Array<Group>,
}

// JSON Test Data
var subjectsData: Array<Subject> = require('./subjects.json');
var groupsData: Array<Groups> = require('./groups.json');

export default function SBFilter() {
    // filters state
    const [filters, setFilters] = useLocalStorageState<Array<ApiFilter>>([], "sufi");

    // input
    const [subjects, setSubjects] = React.useState<Array<Subject>>(subjectsData);
    const [groups, setGroups] = React.useState<Groups | null>(null);

    // input fields 
    const [subject, setSubject] = React.useState<string>('');
    const [group, setGroup] = React.useState<string>('');

    // editor mode
    const [editingMode, setEditingMode] = React.useState<boolean>(false);
    const [targetedFilter, setTargetedFilter] = React.useState<ApiFilter | null>(null);
    const [deleteVisibility, setDeleteVisibility] = React.useState<boolean>(true);

    // validation input
    const [validationMessage, setValidationMessage] = React.useState<string | null>(null);

    const deleteVisible = () => {
        setDeleteVisibility(true);
    }
    const deleteInvisible = () => {
        setDeleteVisibility(false);
    }

    const handleSubjectInput = (event: SelectChangeEvent) => {
        let tsubject = event.target.value as string;
        if (subject !== tsubject) {
            changeSubject(tsubject);
            resetValidationMessage();
        }
    };

    const handleGroupInput = (event: SelectChangeEvent) => {
        let tgroup = event.target.value as string;
        if (group !== tgroup) {
            setGroup(event.target.value as string);
            resetValidationMessage();
        }
    };

    const changeSubject = (sid: string) => {
        setSubject(sid);
        changeAvailableGroups(sid);
        resetGroup();
    };

    const resetSubject = () => {
        setSubject('');
        resetAvailableGroups();
        resetGroup();
    };

    const changeAvailableGroups = (sid: string) => {
        setGroups(groupsData.filter((group) => { return group.id === sid })[0]);
    };

    const resetAvailableGroups = () => {
        setGroups(null);
    };

    const changeGroup = (group_id: string) => {
        setGroup(group_id);
    };

    const resetGroup = () => {
        setGroup('');
    }

    const changeValidationMessage = (message: string) => {
        setValidationMessage(message);
    };

    const resetValidationMessage = () => {
        setValidationMessage(null);
    };

    const enterEditorMode = () => {
        setEditingMode(true);
    };

    const exitEditorMode = () => {
        setEditingMode(false);
        // follows by
        resetSubject();
        resetGroup();
        resetTargetedFilter();
        resetValidationMessage();
    };

    const changeTargetedFilter = (target: ApiFilter) => {
        setTargetedFilter(target);
    }

    const resetTargetedFilter = () => {
        setTargetedFilter(null);
    };

    const handleFilterClick = (event: any) => {
        let [sid, gid] = event.currentTarget.value.split(",");
        //
        changeTargetedFilter(
            {
                sid: sid,
                gid: gid
            }
        );
        //
        enterEditorMode();
        //
        changeSubject(sid);
        changeGroup(gid)
    };

    const handleSaveChangesButton = () => {
        if (subject !== '') {
            if (group !== '') {
                const _filter = { sid: subject, gid: group }
                if (!filters.some(filter => filter.sid === subject && filter.gid === group)) {
                    let _filters = filterOutFilterTarget();
                    setFilters([..._filters, _filter]);
                    // follows by
                    resetSubject();
                    resetGroup();
                    resetTargetedFilter();
                    exitEditorMode();
                } else {
                    changeValidationMessage("gleicher Filter wurde bereits hinzugefgt");
                }
            } else {
                changeValidationMessage("whle einen Kurs");
            }
        } else {
            changeValidationMessage("whle einen Fach");
        }
    }

    const handleDeleteButton = () => {
        let _filters = filterOutFilterTarget();
        setFilters([..._filters]);
        //
        resetSubject();
        resetGroup();
        resetValidationMessage();
        resetTargetedFilter();
        exitEditorMode();
    };

    const filterOutFilterTarget = () => {
        // delete targeted filter if target exists
        let _filters: Array<ApiFilter> = targetedFilter == null ? filters : filters.filter(
            _filter => {
                const sid = _filter.sid === targetedFilter.sid;
                const gid = _filter.gid === targetedFilter.gid;
                return !(gid && sid)
            }
        );
        return _filters;
    };

    return (
        <Container disableGutters>
            <Box
                sx={{
                    paddingY: "20px",
                    display: "flex",
                    flexWrap: "wrap",
                    padding: "8px",
                }}
            >
                {filters.map((filter) => {
                    let sid = filter.sid;
                    let gid = filter.gid;
                    return (
                        <Button
                            sx={{
                                display: "inline-flex",
                                minWidth: "max-content",
                                margin: "8px"
                            }}
                            value={sid + "," + gid}
                            variant="contained"
                            onClick={
                                (e) => handleFilterClick(e)
                            }
                        >
                            <Typography marginRight="10px">
                                {sid}
                            </Typography>
                            <Divider orientation="vertical" />
                            <Typography marginLeft="10px">
                                {gid}
                            </Typography>
                        </Button>
                    )
                })}
            </Box>
            <Box
                sx={{
                    display: "flex",
                    alignItems: "center",
                    paddingY: "20px",
                    paddingTop: "10px"
                }}
            >
                <Container
                    sx={{
                        display: "flex",
                        justifyContent: "center",
                        flexDirection: "column",
                        alignItems: "center"
                    }}
                >
                    {editingMode ?
                        <Container
                            sx={{
                                display: "flex",
                                justifyContent: "center",
                                flexDirection: "column",
                                alignItems: "center"
                            }}
                        >
                            <div style={{
                                width: "100%",
                                display: "flex",
                                justifyContent: "space-around",
                                margin: "0 12px"
                            }}
                            >
                                <FormControl
                                    sx={{
                                        backgroundColor: (theme) => theme.palette.background.default
                                    }}
                                    fullWidth
                                >
                                    <InputLabel id="subject-select-label">
                                        {"Fach"}
                                    </InputLabel>
                                    <CustomSelect
                                        labelId="subject-select-label"
                                        id="subject-select"
                                        value={subject}
                                        label="Subject"
                                        onChange={handleSubjectInput}
                                    >
                                        {
                                            subjects.map((subject) => {
                                                return <MenuItem value={subject.id}>{subject.name}</MenuItem>
                                            })
                                        }
                                    </CustomSelect>
                                </FormControl>
                                <Divider orientation="vertical" sx={{ padding: "5px" }} />
                                <FormControl
                                    sx={{
                                        backgroundColor: (theme) => theme.palette.background.default
                                    }}
                                    fullWidth
                                >
                                    <InputLabel id="subject-select-label">
                                        {"Gruppe"}
                                    </InputLabel>
                                    <CustomSelect
                                        labelId="groups-select-label"
                                        id="groups-select"
                                        value={group}
                                        label="Group"
                                        onChange={handleGroupInput}
                                        disabled={groups == null}
                                    >
                                        {
                                            groups != null
                                                ?
                                                groups.group.map((group) => {
                                                    return (
                                                        <MenuItem value={group.id}>
                                                            {group.name}
                                                        </MenuItem>
                                                    )
                                                })
                                                : (
                                                    <MenuItem disabled>
                                                        {"Ldt . . ."}
                                                    </MenuItem>
                                                )
                                        }
                                    </CustomSelect>
                                </FormControl>
                            </div>
                            <Divider sx={{ margin: "10px" }} flexItem />
                            {
                                validationMessage
                                    ?
                                    <Typography variant="caption" color="error">
                                        {validationMessage}
                                    </Typography>
                                    : null
                            }
                        </Container>
                        : null
                    }
                    <Box
                        sx={{
                            marginTop: "10px",
                            display: "flex",
                            justifyContent: editingMode ? "space-between" : "center",
                            width: "100%",
                        }}
                    >
                        {
                            // placeholder / delete button
                            editingMode ?
                                <Button
                                    sx={{
                                        visibility: (targetedFilter === null || !deleteVisibility) ? "hidden" : "visible",
                                        width: "140px",
                                    }}
                                    color="error"
                                    onClick={
                                        targetedFilter === null ? undefined : handleDeleteButton
                                    }
                                >
                                    {targetedFilter === null ? "hinzufgen" : "lschen"}
                                </Button>
                                : null
                        }
                        <Fab
                            size='small'
                            sx={{
                                transform: editingMode ? "rotate(45deg)" : "",
                                transition: "all 200ms ease-in-out"
                            }}
                            onClick={editingMode ? exitEditorMode : enterEditorMode}
                            color={editingMode ? "error" : "info"}
                        >
                            <AddRoundedIcon />
                        </Fab>
                        {
                            editingMode ?
                                <Button
                                    color="success"
                                    onClick={handleSaveChangesButton}
                                    sx={{
                                        width: "140px"
                                    }}
                                >
                                    {targetedFilter === null ? "Hinzufgen" : "Besttigen"}
                                </Button>
                                : null
                        }
                    </Box>
                </Container>
            </Box >
        </Container >
    )
};

// style override
const CustomSelect = (props: any) => {
    return (
        <Select
            MenuProps={{
                sx: {
                    display: "flex",
                    width: "100%",
                    justifyContent: "center",
                    alignItems: "center"
                },
                BackdropProps: {
                    sx: {
                        backgroundColor: (theme) => alpha(theme.palette.background.default, .98)
                    }
                }
            }}
            {...props}
        >
            {props.children}
        </Select>
    )
};