import React from 'react';

// MUI
import { AppBar, Container } from '@mui/material';

// SUBS
import Substitution from './Substitution';
import Dashboard from './Dashboard';
import Schedule from './Schedule';

// TYPES
import { SculisScreen, SculisTheme } from '../../../App';


type SculisAppBarProps = {
    screen: SculisScreen,
    handleModeChange: Function,
    mode: SculisTheme,
    sdDate: Date,
    sdDateSetter: Function
}

export default function SculisAppBar({ screen, handleModeChange, mode, sdDate, sdDateSetter }: SculisAppBarProps) {

    return (
        <Container disableGutters>
            <AppBar position="static">
                {
                    screen === 'dashboard-screen' ? (
                        <Dashboard handleModeChange={handleModeChange} mode={mode} />
                    ) : screen === 'schedule-screen' ? (
                        <Schedule />
                    ) : screen === 'substitution-screen' ? (
                        <Substitution date={sdDate} dateSetter={sdDateSetter} />
                    ) : (
                        null
                    )
                }
            </AppBar>
        </Container>
    )
};


