// React
import React from 'react';

// MUI
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';

// Icons
import CompareArrowsRoundedIcon from '@mui/icons-material/CompareArrowsRounded';
import HomeRoundedIcon from '@mui/icons-material/HomeRounded';
import CalendarTodayRoundedIcon from '@mui/icons-material/CalendarTodayRounded';

// Types
import { SculisScreen } from '../../../App';

type SculisBottomNavigationProps = {
    dashboardScreenCallback: Function;
    scheduleScreenCallback: Function;
    substitutionScreenCallback: Function;
    // 
    forScreen: SculisScreen
}

export default function SculisBottomNavigation(props: SculisBottomNavigationProps) {
    const handleScreenChange = (event: React.SyntheticEvent, newScreen: SculisScreen) => {
        if (newScreen === 'dashboard-screen') {
            props.dashboardScreenCallback();
        } else if (newScreen === 'schedule-screen') {
            props.scheduleScreenCallback();
        } else if (newScreen === 'substitution-screen') {
            props.substitutionScreenCallback();
        }
    };

    return (
        <BottomNavigation value={props.forScreen} onChange={handleScreenChange} >

            <BottomNavigationAction label="Plan"
                disableRipple
                value="schedule-screen"
                sx={{
                    "&.Mui-selected": {
                        fill: "url(#linearColors)"
                    }
                }}

                icon={
                    <CalendarTodayRoundedIcon />}
            />
            <BottomNavigationAction label="bersicht"
                disableRipple
                value="dashboard-screen"
                icon={<HomeRoundedIcon />}
            />
            <BottomNavigationAction label="Vertretung"
                disableRipple
                value="substitution-screen"
                icon={<CompareArrowsRoundedIcon />}
            />
        </BottomNavigation >
    )
}