import React from 'react';

// MUI
import {
    Button,
    Card,
    CardContent,
    List,
    ListItem,
} from "@mui/material";

// ICONS
import ExpandMoreRoundedIcon from '@mui/icons-material/ExpandMoreRounded';
import ExpandLessRoundedIcon from '@mui/icons-material/ExpandLessRounded';

// SUBS
import Entry from "./Entry";

// TYPES
import { ApiEntry } from './DataTypes';

type BlockProps = {
    class_: string,
    entries: Array<ApiEntry>
}

export default function Block({ class_, entries }: BlockProps) {
    const [open, setOpen] = React.useState<boolean>(false);

    const openSwitch = () => {
        setOpen(!open);
    };

    return (
        <div
            style={{
                display: "flex",
                flexDirection: "column",
                width: "100%"
            }}
        >
            <Card
                sx={{
                    borderRadius: "999px",
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                    justifyContent: "center"
                }}
                onClick={openSwitch}
            >
                <Button
                    sx={{
                        width: "100%",
                        margin: "0",
                        padding: "0",
                        opacity: ".75"
                    }}

                    variant='text'
                >
                    {class_}
                    {
                        open ? (
                            <ExpandLessRoundedIcon />
                        ) : (
                            <ExpandMoreRoundedIcon />
                        )
                    }
                </Button>
            </Card>


            <Card
                sx={{
                    borderRadius: '25px',
                    marginTop: "5px",
                    marginBottom: "20px",
                    display: "flex"
                }}

                variant="elevation"
                elevation={1}
            >
                {
                    open ? (
                        <CardContent sx={{ width: "100%" }}>
                            <List>
                                {
                                    entries.map(
                                        entry =>
                                            <ListItem disableGutters>
                                                <Entry
                                                    entry={entry}
                                                />
                                            </ListItem>
                                    )
                                }
                            </List>
                        </CardContent>
                    ) : (
                        null
                    )
                }
            </Card>
        </div >
    )
};
