import React from "react";

import { Paper } from "@mui/material";
import { SculisScreen } from "../../../App";

import Substitution from './Substitution';
import Dashboard from './Dashboard';
import Schedule from './Schedule';


type SculisBodyProps = {
    screen: SculisScreen,
    date: Date
}

export default function SculisBody({ screen, date }: SculisBodyProps) {

    return (
        <Paper
            sx={{
                height: "100%", overflowY: 'auto', borderRadius: 0
            }}
        >
            {
                screen === 'dashboard-screen' ? (
                    <Dashboard />
                ) : screen === 'schedule-screen' ? (
                    <Schedule />
                ) : screen === 'substitution-screen' ? (
                    <Substitution date={date} />
                ) : (
                    null
                )
            }
        </Paper>
    );
}

